import { NotificationSchedule } from "@rp/common/api-types"
import { awsCron } from "@rp/common/utils"
import { SxProps } from "@rp/react/web/mui/core"

type CreateHandleClickProps = {
  setSchedule: (schedule: NotificationSchedule) => void
  schedule: NotificationSchedule
  day: { value: number; label: string }
}

const handleScheduleDayClick = ({ setSchedule, schedule, day }: CreateHandleClickProps): void => {
  const days = awsCron.expressionToDays(schedule.schedulerExpression)

  const { minute, hour } = awsCron.expressionToTime(schedule.schedulerExpression)
  const daysOfWeek = days.includes(day.value)
    ? days.filter(d => d !== day.value)
    : days.concat(day.value)

  const newSchedule = {
    ...schedule,
    schedulerExpression: awsCron.timeToExpression({
      recurrence: "weekly",
      daysOfWeek,
      hour,
      minute,
    }),
  }

  setSchedule(newSchedule)
}

const getWrapperSx = (isMobile: boolean): SxProps => ({
  display: "flex",
  flexWrap: isMobile ? "wrap" : undefined,
  gap: 1,
  mt: 1,
  "& button": {
    fontSize: isMobile ? 13 : 16,
    borderRadius: 6,
    height: isMobile ? 28 : 40,
    width: isMobile ? 50 : undefined,
  },
})

export { handleScheduleDayClick, getWrapperSx }
